@charset "utf-8";

*{margin:0; padding:0;}
body{color: #333;}

.yb_tab_con_wrapper{
  width: 1200px;
  margin: 0 auto;
}

.tab_list{
  list-style: none;
  display: flex;
  justify-content: center;
}
.tab_list .tab{
  padding: 10px 20px;
  cursor: pointer;
}
.tab_list .active{
  color:#4567ad;
  font-weight: 450;
}


.con_area{
  margin-top: 20px;
  position: relative;
}
.con_area .content{ 
  position: absolute; 
  display: flex;
  gap: 10px;  
}
.con_area .content div img{
  width: 100%;
}
.con_area .content div .name{
  width: 250px; height: 50px;
  margin: 10px auto;
  font-weight: 400;
  display: -webkit-box;
  line-clamp: 2;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
.con_area .content div .price{
  text-align: right;
  color:#FF5C46;
  font-weight: bold;
  font-size: 20px;
}
.con_area .content div .price span{
  font-weight: normal;
  font-size: 12px;
  margin-left: 5px;
}
